<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <el-input placeholder="请输入姓名" class="w_200" icon="search" v-model="searchWord" :on-icon-click="search"></el-input>
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table class="table" v-loading="listLoading" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="userName" label="姓名">
                    </el-table-column>
                    <el-table-column align="center" prop="userId" label="用户ID">
                    </el-table-column>
                    <el-table-column align="center" prop="creditScore" label="信用分">
                    </el-table-column>
                    <el-table-column align="center" prop="identityScore" label="身份信息">
                    </el-table-column>
                    <el-table-column align="center" prop="outScore" label="外部信用">
                    </el-table-column>
                    <el-table-column align="center" prop="useCarScore" label="用车行为">
                    </el-table-column>
                    <el-table-column align="center" prop="creditStatus" label="状态" width="120" class="{red: scope.row.userStatus == 2}">
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="180">
                        <template scope="scope">
                            <el-button type="info" size="small" @click="editItem(scope.$index, scope.row)">查看详情</el-button>
                            <el-button size="small"type="danger" @click="blackAndActivate(scope.$index, scope.row)">拉黑</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    
        <el-dialog class="edit dialog-info" size="large" title="用户信用" :visible.sync="editDialog">
            <div class="info-content">
                <el-row>
                    <el-col :span="6">用户ID：<span>{{eForm.userId}}</span></el-col>
                    <el-col :span="6">姓名：<span>{{eForm.userName}}</span></el-col>
                    <el-col :span="6">信用分：<span>{{eForm.creditScore}}</span></el-col>
                    <el-col :span="6">身份信用：<span>{{eForm.identityCredit}}</span></el-col>
                    <el-col :span="12">个人资料：<span>{{getUserInfo(eForm) == 0? '完整': '不完整'}}</span></el-col>
                    <el-col :span="12">车辆信息：<span>{{getCarInfo(eForm) == 0? '完整': '不完整'}}</span></el-col>
                    <el-col>外部信用：{{eForm.outCredit}}</el-col>
                <el-row>
                    <el-col>已绑定：
                        <span v-for="item in eForm.isBinding" :key="index">{{item + '　'}}</span>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col>未绑定：
                        <span :span="5" v-for="item in eForm.notBinding" :key="index">{{item + '　'}}</span>
                    </el-col>
                </el-row>
                    <el-col>用车信用：<span>{{eForm.useCarCredit}}</span></el-col>
                    <el-col>订单号：</el-col>
                    <el-col :push='1' v-for="(item, index) in eForm.order" :key="index">{{`${index + 1}： ${item}`}}</el-col>
                </el-row>
            </div>

            <div class="info-radio">
                <el-radio-group v-model="eForm.userStatus">
                    <el-radio class="radio" :label="1">激活</el-radio>
                    <el-radio class="radio" :label="2">拉黑</el-radio>
                    <!-- <el-radio class="radio" :label="3">驳回</el-radio> -->
                    <!-- <el-radio class="radio" :label="4">冻结</el-radio> -->
                </el-radio-group>
            </div>

            <div slot="footer" class="dialog-footer">
                <el-button @click="editDialog = false">取 消</el-button>
                <el-button type="primary" @click="editSave()">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            word: '',
            searchWord: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            index: null,
            tableData: [],
            editDialog: false, //弹出框-编辑
            eForm: {
            },
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            // 表格距离顶部的高度
            tableTop: 0,
        }
    },
    props: ['API'],
    computed: {
        // 计算表格高度
        tableMaxHeight() {
            this.$store.commit('setTableMaxHeight', this.tableTop)
            return this.$store.state.tableMaxHeight
        }
    },
    methods: {
        search(ev) {
            this.word = this.searchWord
            this.currentPage = 1
            this.getList(this.word);
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.word)
        },
        // 格式化时间
            getTime(time) {
                return this.$common.getNowTime(time)
            },
            // 格式化用户资料
            getUserInfo(form) {
                if (!form.birthday || !form.idType || !form.gender || !form.userPhone || !form.avater || !form.userName || !form.idNumber || !form.driving || !form.idPicture || !form.constellation || !form.userType) return 1
                return 0
            },
            // 格式化车辆资料
            getCarInfo(form) {
                if (!form.address || !form.carName || !form.carSeries || !form.carStatus || !form.city || !form.collection || !form.color || !form.seatNumber || !form.brand || !form.modelCharacter || !form.orderQuantity || !form.registerTime || !form.vehicleAge || !form.model || !form.manualOrAutomatic || !form.licensePlateNumber) return 1
                return 0
            },
        // 查看-编辑
        editItem(index, row) {
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "user_uuid": row.userUuid
                }
            }
            this.$common._post('user/creditDetail', json, this, data => {
                this.editDialog = true
                this.eForm= Object.assign({}, data)
            })
        },
        editSave(){
            this.$confirm('是否确认修改?', '提示', {})
                .then(() => {
                    var json = {
                        "head": this.$common.getUserStorage(),
                        "body": {
                           "user_uuid": this.eForm.user_uuid,
                           "user_status": this.eForm.userStatus
                        }
                    }
                    this.$common._post('user/blackAndActivate', json, this, data => {
                        this.editDialog = false
                        this.$message({ type: 'success', message: data.description })
                        this.getList()
                    })
                }).catch(() => {})
        },
        //拉黑
        blackAndActivate(index, row) {
            this.$confirm('是否确认修改?', '提示', {})
                .then(() => {
                    var json = {
                        "head": this.$common.getUserStorage(),
                        "body": {
                           "user_uuid": row.userUuid,
                           "user_status": 2
                        }
                    }
                    this.$common._post('user/blackAndActivate', json, this, data => {
                        this.$message({ type: 'success', message: data.description })
                        this.getList()
                    })
                }).catch(() => {})
        },
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": this.$common.getUserStorage(),
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelUserCredit', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList(this.word);
        },
        // 获取列表
        getList(userName = ''){
            this.listLoading = true
            var json = {
                "head": this.$common.getUserStorage(),
                "body": {
                   "pageIndex": this.currentPage,
                   "pageSize": this.pageSize,
                   "userName": userName,
                   "orderBy": this.orderBy,
                }
            }
            this.$common._post('user/creditList', json, this, data => {
                this.total = data.total
                this.tableData = data.userCreditList
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.getList()
        // 表格距离顶部的高度
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style scoped lang="scss">
.personalInfo-box {
    img {
        width: 100px;
        vertical-align: top;
    }
}
</style>



